import { decryption } from "../Encryption/decrypt";
import { PATCH } from "../UrlConfig/MethodType";

export const userActivatedMemberFunction = (builder) => {
  return builder.query({
    query: (args) =>
      `/v2/extra/${args?.uid}/active/member/role?active_member_role=${
        args?.role ?? ""
      }`,
    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};
export const staffAllDesignationListFunction = (builder) => {
  return builder.query({
    query: (sid) => `/v2/user/staffdesignationdata/${sid}`,
    transformResponse: (response) => {
      let encrypt = decryption(response?.encrypt);
      return encrypt?.staff;
    },
    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};
export const studentAllDesignationListFunction = (builder) => {
  return builder.query({
    query: (sid) => `/v2/user/studentdesignationdata/${sid}`,
  });
};
export const userMemberAllAccountFunction = (builder) => {
  return builder.query({
    query: (uid) => `/v2/user/${uid}/staff/student/role`,
    transformResponse: (response) => response?.role_query,
  });
};

// start hew for process
export const userMemberAppliedAllApplicationAccountFunction = (builder) => {
  return builder.query({
    query: (uid) => `/v2/user/${uid}/all/application/query`,
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    transformResponse: (response) => response?.all_apps,
    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};
export const userMemberAppliedOneApplicationStatusFunction = (builder) => {
  return builder.query({
    query: (data) => `/v2/user/${data.uid}/all/${data.appId}/application/query`,
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};
export const libraryDepartmentBookFilterFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/v2/feed/filter/${data.lid}/by/department/query`,
      method: PATCH,
      body: data.filterBody,
    }),
  });
};

export const userDashboardActiveApplicationFunction = (builder) => {
  return builder.query({
    query: (args) => `/v2/user/${args?.uid}/dashboard/all/application/query`,
    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};

// for member tab v1 support api

export const userV1ActivatedMemberFunction = (builder) => {
  return builder.query({
    query: (args) =>
      `/v1/extra/${args?.uid}/active/member/role?active_member_role=${
        args?.role ?? ""
      }`,
    keepUnusedDataFor: 300,
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    transformResponse: (response) => response?.role,
    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};
