import { useTranslation } from "react-i18next";
import style from "./Navbar.module.css";
import { useDispatch, useSelector } from "react-redux";
import { useUserV1ActivatedMember } from "../../Hooks/ApiHooks/UserMember/user-member-api";
import UserNavbarMemberDesignation from "./UserNavbarMemberDesignation";
// import Loading from "../../Loader/Loading";
import { memo, useEffect, useState } from "react";
import { idChangeAction } from "../../../redux/store/department-slice";
import { Link, useParams } from "react-router-dom";
const UserNavbarMember = ({ onDispatchActions, status, parentStyle }) => {
  const { t } = useTranslation();
  const params = useParams();
  const [step, setStep] = useState(false);
  const userId = useSelector((state) => state.idChange.id);
  const userMember = useSelector((state) => state.idChange.user_member);
  const dispatch = useDispatch();
  const { userV1ActivatedMember } = useUserV1ActivatedMember({
    data: {
      uid: userId,
      role: "",
    },
    skip: userMember?.isCallable ? true : !userId,
  });

  useEffect(() => {
    if (userV1ActivatedMember?.member && userV1ActivatedMember?.activeRole) {
      dispatch(
        idChangeAction.onUserMember({
          memberType: userV1ActivatedMember?.member,
          memberId: userV1ActivatedMember?.activeRole,
          isCallable: true,
        })
      );
    }
  }, [
    userV1ActivatedMember?.member,
    userV1ActivatedMember?.activeRole,
    dispatch,
  ]);
  //   console.info("userV1ActivatedMember", userV1ActivatedMember);
  //   console.info("userMember", userMember);
  const onMemberClick = () => {
    setStep((pre) => !pre);
    onDispatchActions();
  };
  return (
    <>
      {userV1ActivatedMember?.member === "Student" ||
      userMember?.memberType === "Student" ? (
        <Link
          to={`/q/${params?.username}/memberstab`}
          onClick={onDispatchActions}
        >
          <div className={parentStyle}>
            {status === "members" ? (
              <img alt="not found" src="/images/members-dark-icon.svg" />
            ) : (
              <img alt="not found" src="/images/members-icon.svg" />
            )}
            <p>{t("members")}</p>
          </div>
        </Link>
      ) : (
        <section onClick={onMemberClick} className={style.unm}>
          {step ? (
            <>
              <div className={style.unmc}>
                <div className={style.unmc_inner}>
                  {status === "members" ? (
                    <img alt="not found" src="/images/members-dark-icon.svg" />
                  ) : (
                    <img alt="not found" src="/images/members-icon.svg" />
                  )}
                  <p>{t("members")}</p>
                </div>

                <div className={style.unmlc_triangle_click} />
              </div>
              <section className={style.unm_list_container_click}>
                {/* {userV1ActivatedMemberLoading && <Loading />} */}
                <UserNavbarMemberDesignation
                  staffId={
                    userV1ActivatedMember?.activeRole
                      ? userV1ActivatedMember?.activeRole
                      : userMember?.memberId
                  }
                />
              </section>
            </>
          ) : (
            <>
              <div className={style.unmc}>
                <div className={style.unmc_inner}>
                  {status === "members" ? (
                    <img alt="not found" src="/images/members-dark-icon.svg" />
                  ) : (
                    <img alt="not found" src="/images/members-icon.svg" />
                  )}
                  <p>{t("members")}</p>
                </div>

                <div className={style.unmlc_triangle} />
              </div>
              <section className={style.unm_list_container}>
                {/* {userV1ActivatedMemberLoading && <Loading />} */}
                <UserNavbarMemberDesignation
                  staffId={
                    userV1ActivatedMember?.activeRole
                      ? userV1ActivatedMember?.activeRole
                      : userMember?.memberId
                  }
                />
              </section>
            </>
          )}
        </section>
      )}
    </>
  );
};

export default memo(UserNavbarMember);
